<template>
  <div>
    <audio ref="music" loop>
      <source src="../assets/yd.mp3" type="audio/mpeg" />
    </audio>
  </div>
</template>

<script>
import bus from '@/utils/bus'
export default {
  created () {
    let music_play = this.$refs.music

    bus.$on('music', msg => {
      let music_play = this.$refs.music

      if (msg == 2) {
        music_play.pause()
      } else {
        music_play.play()
      }
    })
  },
  mounted () {
    this.$nextTick(() => {
      let music_play = this.$refs.music
      music_play.play()
    })
  }
}
</script>

<style lang="scss" scoped>
</style>
